
import slide from 'components/slide/index'
import http from 'js/http'
import Qrcode from 'vux/src/components/qrcode/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import {mapState, mapMutations, mapGetters} from 'vuex'

export default {
name: "index",
directives: {
TransferDom
},
components: {
slide,
Qrcode,
Confirm
},
data() {
return {
applyUrl: 'http://uaq5pzd9vm1kxhdk.mikecrm.com/6aNQyf2',
agree: false,
idfConfirmShow: false,
nodeConfirmShow:false
}
},
methods: {
goSubmit() {
if (!this.agree) {
this.$vux.toast.show('请先阅读并同意节点申请协议')
return
}
if (this.myNodeInfo.status !== -1) {
this.nodeConfirmShow = true
return
}

if (this.identifyMsg.status !== 1) {
this.idfConfirmShow = true
return
}

this.$router.push({
path: '/personal/applynode/submit'
})
},
onIdfConfirm() {
this.$router.push({
path: '/personal/identify/' + this.identifyPath
})
},
onNodeConfirm() {
this.$router.push({
path: '/personal/node/' + this.nodePath
})
}
},
computed: {
identifyPath() {
if (!this.identifyMsg) return ''
if (!this.identifyMsg.isIdentify) return 'submit'
switch (this.identifyMsg.status) {
case 0:
return 'wait'
case 1:
return 'success'
case 2:
return 'fail'
}
},
nodePath(){

if (!this.myNodeInfo) return ''
switch (this.myNodeInfo.status) {
case 1:
return 'index'
case 2:
return 'wait'
case 4:
return 'fail'
}
},
...mapGetters([
"identifyMsg",
"myNodeInfo"
]),
},

}



